












































  li.book-item {
  display: flex;
  .van-image {
    vertical-align: middle;
  }
  .info {
    flex: 1;
    margin-left: 10px;

    line-height: 20px;
    font-size: 12px;
    h5.name {
      .title{
        line-height: 20px;
      font-size: 14px;
      }
      // .complete{
      //   background-color: green;
      //   display: inline-block;
      //   margin-left: 10px;
      //   border-radius: 2px;
      //   font-style: normal;
      //   color: #fff;
      //   font-size: 10px;
      //   padding: 1px 2px;
      // }
    }
    .desc {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;

      margin: 5px 0;
    }
    .category {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        color: #666;
        &.split {
          margin: 0 5px;
        }
        &.c-red {
          color: #b83320;
        }
      }
      i.tag {
        font-size: 10px;
        font-style: normal;
        border: 1px solid #ff8400;
        // float: right;
        line-height: 1.2em;
        padding: 0 3px;
        border-radius: 3px;
        color: #ff8400;
      }
    }
  }
}
